if (typeof Object.assign != 'function') {
  Object.assign = function(target) {
    'use strict';
    if (target == null) {
      throw new TypeError('Cannot convert undefined or null to object');
    }

    target = Object(target);
    for (var index = 1; index < arguments.length; index++) {
      var source = arguments[index];
      if (source != null) {
        for (var key in source) {
          if (Object.prototype.hasOwnProperty.call(source, key)) {
            target[key] = source[key];
          }
        }
      }
    }
    return target;
  };
}



class Common {
    constructor(){

    };
    init (){
        const rule = document.querySelector('.rule'),
              self = this;
              rule.addEventListener("click", function(){
                  self.popup("rule-pop");
              })
              self.popupClose();  

    }
    addEvent (el, type, fn, capture) {
        if (window.addEventListener) {
            el.addEventListener(type, function(e) {
                fn.call(el, e);
            }, capture);
        } else if (window.attachEvent) {
            el.attachEvent("on" + type, function(e) {
                fn.call(el, e);
            });
        } 
    }
    popup (ele){
        let items = document.querySelectorAll('popup');
        for(let i = 0; i < items.length; i++){
            let item = items[i];
            item.className = 'popup transition hidden';
        }
        document.getElementById(ele).className = 'popup';
    }
    popupClose (){
        let items = document.querySelectorAll('.close');
        for(let i = 0; i < items.length; i++){
            let item = items[i];
            item.onclick = function(e){  
                let parentNode = this.parentNode.id;
                document.getElementById(parentNode).className = 'popup transition hidden';
            } 
        }
    }
}

